.modal {
  position: fixed;
  justify-content: center;
  visibility: hidden;
  pointer-events: none;
  inset: 0; /* top:0 right:0 bottom:0 left:0 */
  display: grid; /* hidden until opened */
  align-items: center;
  /* Use one background declaration */
  background: rgba(0, 0, 0, 0.5); /* dim backdrop */
  padding: 1rem; /* breathing room on small screens */
  /* place-items: center;  grid-centers the content */
  /* padding-block-start: clamp(16px, 20vh, 207px);
     padding-inline: clamp(16px, 33vw, 355px);
     padding-block-end: clamp(16px, 20vh, 207px); */
  opacity: 0;
  transition:
    background-color .53s ease,
    opacity .53s ease,
    visibility 0s linear .53s;
}

.modal_type_preview {
  padding: 0;
}

.modal_opened {
  visibility: visible;
  display: flex;
  pointer-events: auto;
  opacity: 1;
  transition:
    background-color .53s ease .04s,
    opacity .53s ease .04s,
    visibility 0.5s;
}

.modal__container {
  inline-size: min(730px, 100%);
  /* height hugs content, but never taller than 480px or viewport */
  /* max-width: 730px; */
  /* max-block-size: min(480px, 90vh); */
  /* if content is taller than the cap, scroll inside */
  /* max-width: 730px; */
  overflow: auto;
  box-sizing: border-box;
  border-radius: 8px;
  background-color: #fff;
  position: relative;
  padding: 32px 50px;
  transform: translateY(8px) scale(.985);
  opacity: 0;
  transition:
    opacity .53s ease .08s,
    transform .57s cubic-bezier(.2,.7,.2,1) .06s;
  will-change: transform, opacity;
}

.modal_opened .modal__container {
  transform: translateY(0) scale(1);
  opacity: 1;
}

.modal__close-btn {
  background: transparent url("../images/Close.svg") no-repeat center center;
  border: none; /* no box */
  padding: 4px 6px; /* bigger click target, still invisible */
  margin: 0;
  font: inherit;
  font-size: 1.25rem; /* match/compliment H2 size */
  height: 40px;
  width: 40px;
  color: #666; /* subtle by default */
  cursor: pointer;
  opacity: 1;
  transition: opacity .63s ease, transform .63s ease;
}

.modal__close-btn:hover {
  opacity: 0.5;
  transform: scale(1.1);
}

/* If you intend the same element to have both classes, use .modal__close-btn.modalclose-btn_type_preview */


.modal__header {
  display: flex;
  align-items: center; /* vertically aligns with the h2 */
  justify-content: space-between;
}

.modal__title {
  display: flex;
  width: 100%;
  align-items: center; /* vertical align with H2 */
  justify-content: space-between;
  gap: 12px;
  font-size: 32px;
  font-weight: 500;
}

.modal__form {
  display: grid;
}

.modal__label {
  display: flex;
  flex-direction: column;
  margin-bottom: 32px;
}

.modal__input {
  display: block;
  box-sizing: border-box;
  border: 1px solid #d0d0d0;
  margin: 8px 0 0;
  width: 100%;
  padding: 15px 0px;
  border-radius: 8px;
  font: inherit;
  height: 48px;
}

.modal__input::placeholder {
  color: #2121214d;
  font-size: 20px;
}
.modal__input_type_error {
  border-color: #d93025;
  background-color: #fff6f6;
}

.modal__input_type_error,
.modalinput_type_error {
  border-color: #d93025;            
  outline: 2px solid rgba(217,48,37,.2); 
  background-color: #fff6f6; 
  display: block;      
}

.modal__submit-btn {
  padding: 18px 32px;
  border: 0;
  border-radius: 8px;
  gap: 8px;
  background: #000;
  color: #fff;
  font-weight: 400;
  cursor: pointer;
  justify-self: flex-start;
  max-width: 156px;
  width: 100%;
  transition: background-color 160ms ease, transform 80ms ease, box-shadow 160ms ease;
}

.modal__submit-btn:hover {
  background: rgb(128, 120, 120);
}

.modal__submit-btn_disabled,
.modalsubmit-btn_disabled,
.modal__submit-btn:disabled {
  opacity: 0.45;
  cursor: not-allowed;
  background: #9aa3af;
  color: #f3f4f6;
  pointer-events: none;          /* ignore clicks/hover */
  transform: none;
  box-shadow: none;
}

.modal__submit-btn:not(:disabled):not(.modal__submit-btn_disabled):not(.modalsubmit-btn_disabled):hover {
  background: rgb(128, 120, 120);
  transform: scale(1.01);
} 

.modal__error,
.modalerror {
  display: block; 
  visibility: hidden;           /* hide text but preserve layout if needed */
  margin-top: 0;           /* remove spacing when hidden */
  color: red;
  min-height: 0;           /* avoid unwanted space */
  font-size: 12px;
}
.modal__error_visible,
.modalerror_visible {
  visibility: visible; 
  margin-top: 8px;         /* add spacing only when shown */
}

.modal__image-container {
  position: relative; /* anchors the absolute close button */
}

.modal__image {
  max-width: 80vw;
  max-height: 80vh;
  width: 100%;
  object-fit: contain;
  display: block;
  border-radius: 8px;
}

.modal__close-btn_type_preview {
  top: 0px;
  right: -52.5px;
  position: absolute;
  filter: invert(1);
  opacity: 1;
}

.modal__caption {
  margin-top: 40px;
  position: absolute;
  left: 0;
  top: calc(100% - 32px);
  max-width: 214px;
  max-height: 32px;
  padding: 4px 8px;
  display: inline-flex;
  align-items: center;
  background: rgba(0, 0, 0, 1);
  color: #fff;
  border-radius: 8px;
  font-size: 14px;
  line-height: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  pointer-events: none;
}

@media screen and (max-width: 360px) {
  .modal {
    padding-left: 10px;
    padding-right: 10px;
  }

  .modal__container {
    border-radius: 8px;
    /* margin: 222px, 10px; */
    padding: 12px;
    inline-size: 100%;
    overflow: auto;
    box-sizing: border-box;
    background: #fff;
    position: relative;
  }

  .modal__header {
    display: flex;
    align-items: center; /* vertically aligns with the h2 */
    justify-content: space-between;
    height: 40px;
    margin: 0;
  }

  .modal__title{
    font-size: 20px;
    height: 40px;
    margin: 0;
  }

  .modal__form {
    max-width: 100%;
    gap: 24px;
    height: 336px;
    margin-top: 24px;
  }

  .modal__label {
    /* margin: 24px 0; */
    height: 74px;
    /* margin: 12px 0px; */
  }

  .modal__input {
    max-width: 100%;
    inline-size: 100%;
    /* padding: 20px 0px; */
    gap: 24px;
  }

  .modal__submit-btn {
    max-width: 100%;
    gap: 0;
    height: 64px;
  }
}

@media screen and (max-width: 627px) {
  .modal__close-btn_type_preview  {
    top: -48px;
    right: 0px;
    position: absolute;
  }

  .modal__image {
    max-width: unset;
  }
}

